import { Form, Button } from "react-bootstrap"
import styled from "@emotion/styled"
import React from "react"
import emailjs from "emailjs-com"

const FormaGolf = styled(Form)`
  font-family: "Roboto", serif;
  padding-top: 2rem;
`

const Forma = () => {
  function sendEmail(e) {
    e.preventDefault()

    emailjs
      .sendForm(
        "gmail",
        "forma_de_contacto",
        e.target,
        "user_CltxLftStOVHdUg5c2mei"
      )
      .then(
        result => {
          console.log(result.text)
        },
        error => {
          console.log(error.text)
        }
      )
  }

  return (
    <>
      <FormaGolf className="contact-form" onSubmit={sendEmail}>
        <Form.Group controlId="name">
          <Form.Label>Your name (required) </Form.Label>
          <Form.Control type="text" name="user_name" />
        </Form.Group>

        <Form.Group controlId="email">
          <Form.Label>Your Email (required) </Form.Label>
          <Form.Control type="email" name="user_email" />
        </Form.Group>
        <Form.Group controlId="subject">
          <Form.Label>Subject </Form.Label>
          <Form.Control type="text" name="subject" />
        </Form.Group>
        <Form.Group class="form-group">
          <Form.Label for="exampleFormControlTextarea1">
            Your Message
          </Form.Label>
          <Form.Control name="message" rows="3" as="textarea" />
        </Form.Group>
        <Button type="submit">SEND</Button>
      </FormaGolf>
    </>
  )
}

export default Forma
